@import '../../../styles/color';
@import '../../../styles/media-query';

.wrap {
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, $RANOLP_OBSIBLUE, $RANOLP_SHINEWHITE);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.createWrap {
  display: flex;
  flex-direction: column;
}

.create {
  color: $RANOLP_RICHDARK;
  font-weight: 500;

  font-size: 4rem;

  transition: font-size 0.5s ease-out;

  @include when-tablet {
    font-size: 3rem;
  }

  @include when-mobile {
    font-size: 2rem;
  }

  @include when-mini-mobile {
    font-size: 1.5rem;
  }

  .highlight {
    text-decoration: underline;
    text-decoration-color: $RANOLP_FIERYORANGE;
    color: $RANOLP_SHINEWHITE;
    font-weight: 900;
  }
}
