@import '../../styles/color';
@import '../../styles/font';
@import '../../styles/media-query';

.title {
  position: absolute;
  top: 16px;

  left: 50%;
  transform: translateX(-50%);

  font-size: 1.5rem;
  font-weight: 900;

  text-transform: uppercase;
  text-orientation: sideways;

  color: $RANOLP_SHINEWHITE;

  letter-spacing: 0.7rem;
  padding-left: 0.7rem;
}

.slideWrap {
  position: relative;
  width: 100vw;
  height: 100vh;

  user-select: none;
}
