@import '../../../styles/color';
@import '../../../styles/media-query';

.wrap {
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, $RANOLP_OBSIBLUE, $RANOLP_SKYBLUE);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.nickname {
  color: $RANOLP_SHINEWHITE;
  font-weight: 400;

  font-size: 8rem;
  padding-left: 2rem;
  letter-spacing: 2rem;

  transition: font-size 0.5s ease-out, padding-left 0.5s ease-out,
    letter-spacing 0.5s ease-out;

  @include when-tablet {
    font-size: 8rem;
  }

  @include when-mobile {
    font-size: 5rem;
  }

  @include when-mini-mobile {
    font-size: 3rem;
    padding-left: 1rem;
    letter-spacing: 1rem;
  }

  .highlight {
    font-weight: 700;
  }
}

.description {
  color: $RANOLP_SHINEWHITE;
  font-size: 3rem;
  font-weight: 100;
  font-style: italic;

  transition: font-size 0.5s ease-out;

  @include when-mobile {
    font-size: 2.5rem;
  }

  @include when-mini-mobile {
    font-size: 1.5rem;
  }

  .highlight {
    font-weight: 400;
    text-decoration: underline;
    text-decoration-color: $RANOLP_FIERYORANGE;
  }
}

.scrollDown {
  position: absolute;
  bottom: 1rem;

  border: 1px solid rgba($RANOLP_SHINEWHITE, 0.6);
  border-radius: 8px;
  padding: 0.5rem 2rem;

  color: rgba($RANOLP_SHINEWHITE, 0.6);

  font-size: 1.5rem;

  @include when-mini-mobile {
    font-size: 1rem;
  }
}
